// @warn "This is dev settings"

@import "settings";
@import "init";

body {
	padding-top: $navigation__height; }

.hero {
	padding: 120px 0;
	text-align: center;
	color: #fff;
	background: {
		color: $secondary-color;
		image: linear-gradient(0deg, $secondary-color 10%, $primary-color 90%);
		size: cover;
		attachment: fixed; } }


.lunaconHeader {
	font-size: 40px; }

.page__header {
	margin-bottom: 60px;
	background: {
		color: $primary-color; }

	h1 {
		color: #fff; } }

.box-fill {
	padding: 5px;
	background: $mute-color;
	border-radius: 3px;
	border: solid 1px darken($mute-color, 5%);
	color: darken($mute-color, 25%);
	font-size: 12px;
	text-align: center;
	margin-bottom: $the__global__padding;
	margin-top: $the__global__padding; }


.padding {
	padding: {
		top: 60px;
		bottom: 60px; }

	&--bottom {
		padding-bottom: 60px; }
	&--top {
		padding-top: 60px; } }

.small-padding {
	padding: 5px $the__global__padding;
	margin-bottom: 5px; }

footer.footer {
	background: $dark-color;
	color: #eee;
	margin-top: 60px; }


.pre {
	text-align: left;
	padding: $the__global__padding;
	border: solid $primary-color 1px;
	border-radius: $global-border-radius;
	background: mix($primary-color, #fff, 2%);
	@extend .ellipsis; }



code {
	border: solid mix($secondary-color, #fff, 25%) 1px;
	border-radius: $global-border-radius;
	background: mix($secondary-color, #fff, 5%);
	padding: 0 5px;
	margin-right: 3px;
	display: inline-block;
	color: $secondary-color; }


pre code {
	overflow-x: hidden !important;
	text-overflow: ellipsis !important; }


h1, h2, h3, h4, h5, h6 {
	@extend .text--primary;
 }	// transition: all 1.5s ease

.hljs {
		background: mix($primary-color, #fff, 3%) !important;
		border: solid 1px mix($primary-color, #fff, 20%); }


.button__list {
	> .btn {
		margin-bottom: 15px;
		margin-right: 5px; } }


.star__block {
	padding: 5px;

	i.lunacon {
		margin-right: 2px;
		font-size: 12px !important; } }


.button {
	@extend .btn;
	@extend .text--primary;
	@extend .background--primary-light;
	@extend .border--primary;

	&:hover {
		background: $primary-color !important;
		color: #fff !important; } }


.bigger-text {
	color: $primary-color;

	[class*="col"] {
		padding: $the__global__padding;


		&:hover {
			@extend .background--primary-light; } }

	i {
		font-size: 24px;
		color: $secondary-color;
		width: 46px; }

	span {
		font-weight: bold;
		letter-spacing: 2px; } }


@import "arduino-light";


.media {
	&__ui {
		background: linear-gradient(180deg, #333, #000);
		list-style: none; }


	&__li i {
		float: left;
		padding: 0;
		margin: 0;
		color: #fff;
		padding: 8px 12px;
		background: linear-gradient(180deg, #333, #000);
		opacity: 1;
		transition: opacity 0.5 ease;

		&:hover {
			opacity: 0.75; } } }
